.layout_ef {
  padding: 0px 15px;
  flex: auto;
}
.layout_ef .ef_content_right_header {
  background-color: #fff;
  padding: 10px;
  width: 100%;
}
.layout_ef .ef_content_right_header .ef_content_right_header_title {
  font-size: 18px;
  line-height: 30px;
  font-weight: 600;
}
.layout_ef .ef_content_right_header .ef_content_right_header_description {
  font-size: 14x;
  line-height: 20px;
}
.layout_ef .ef_content_right_content {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
}
.layout_ef .ef_content_right_content .ef_content_right_content_card {
  width: 30%;
  height: 100%;
  background-color: #fff;
  padding: 10px;
  margin-right: 20px;
  margin-bottom: 20px;
}
.layout_ef .ef_content_right_content .ef_content_right_content_card .ef_content_right_content_card_title {
  font-size: 16px;
  line-height: 25px;
  font-weight: 600;
  word-wrap: break-word;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.layout_ef .ef_content_right_content .ef_content_right_content_card .ef_content_right_content_card_description {
  font-size: 12px;
  line-height: 20px;
  margin: 10px 0px 20px 0px;
  word-wrap: break-word;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
  height: 60px;
}
.layout_ef .ef_content_right_content .ef_content_right_content_card .ef_content_right_content_card_knowledge {
  display: flex;
  display: -webkit-flex;
  line-height: 20px;
  margin: 10px 0px;
}
.layout_ef .ef_content_right_content .ef_content_right_content_card .ef_content_right_content_card_knowledge .ef_content_right_content_card_knowledge_icon {
  flex: 0 0 60px;
}
.layout_ef .ef_content_right_content .ef_content_right_content_card .ef_content_right_content_card_btn {
  margin-top: 20px;
  text-align: center;
  font-weight: 600;
}
.layout_ef .ef_content_right_content .ef_content_right_content_card:hover {
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.18);
  cursor: pointer;
}
.layout_ef .ef_content_right_content .ef_content_right_content_card:hover .ef_content_right_content_card_title {
  color: var(--theme-color);
}
